import { authAPI } from "../api/api";
import { stopSubmit } from "redux-form";

const SET_USER_DATA = "anketa/auth/SET-USER-DATA"

const initialState = {
    usedId: null,
    email: null,
    login: null,
    isAuth: false
}

const AuthReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            }
        default:
            return state;
    }
}

export const setAuthUserData = (userId, email, login, isAuth) => {
    return ({
        type: SET_USER_DATA,
        payload: { userId, email, login, isAuth }
    })
}

export const getAuthUserDataThunkCr = () => async (dispatch) => {
    const response = await authAPI.authMe();
    if (response.data.resultCode === 0) {
        const { id, email, login } = response.data.data;
        dispatch(setAuthUserData(id, email, login, true));
    }
}
export const loginThunkCr = (email, password, rememberMe) => async (dispatch) => {
    const response = await authAPI.login(email, password, rememberMe);
    if (response.data.resultCode === 0) {
        dispatch(getAuthUserDataThunkCr())
    } else {
        const error = response.data.messages.length > 0 ? response.data.messages[0] : "indefinited error"
        dispatch(stopSubmit("login", { _error: error }))
    }
}
export const logoutThunkCr = () => async (dispatch) => {
    const response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }
}

export default AuthReducer;